<template>
  <div>
    <h3>V-model</h3>
    <input type="checkbox" v-model="checked" v-bind:value="value" />
    <p>:{{ checked }}</p>
    <input type="radio" v-model="radioed" name="sex" value="" />
    <input type="radio" v-model="radioed" name="sex" value="" />
    <p>:{{ radioed }}</p>
  </div>
</template>
<script>
export default {
  data() {
    return {
      value: "",
      checked: this.$store.state.jivue.vif,
      radioed: ""
    };
  }
};
</script>
